<template>
    <div class="first-layout">
        <iframe ref="unity" class="simulation-iframe" src="/unity/index.html" @load="onLoad"></iframe>
    </div>
</template>

<script>
//实验仿真
export default {
    name: "Simulation",
    methods: {
        onLoad() {
            let wid = this.$refs.unity.contentWindow;
            //导出的数据
            wid.ExportData = (data) => {
                this.$store.commit('setScore', data);
                this.$store.commit('setDidExp', true);
                console.log('导出-ExportData', data);
            };
        }
    },
};
</script>